@import './variables.scss';

.bg{
    background-image: url(../img/bg.jpg);
    background-size: auto;
    background-repeat: no-repeat;
    background-position-x: left;
    background-position-y: -100px;
    height: 400px;
    position: relative;
    transition: 0.2s;
    .screen{
        background: rgb(255,255,255);
background: -moz-linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(8,8,9,1) 100%);
background: -webkit-linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(8,8,9,1) 100%);
background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(8,8,9,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#080809",GradientType=1);
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100%;
        left: 0;
    }
    h2{
        color: $white;
        position: absolute;
        bottom: 0;
        font-size: 5em;
        z-index: 3;
    }
}